@import url('https://fonts.googleapis.com/css2?family=Anton&family=Bebas+Neue&family=Cabin&family=Ceviche+One&family=Joan&family=Mochiy+Pop+One&family=Noto+Sans:ital,wght@0,500;0,600;1,500&family=Oswald:wght@300&family=Radio+Canada:wght@600&family=Roboto:wght@500;700&family=Ubuntu:wght@300&display=swap');
*{
box-sizing: border-box;
margin: 0;
padding:0;

}


body {
  margin: 0;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
p{
  font-size: 1.6rem;
}
h1{
  font-size: 6rem;
}
.app{
  width:100%;
  height:116vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color:#fff;
}
.app:before{
  content: "";
  background: url('./assests/beach-sunset\ android-iphone-desktop-hd-backgrounds-wallpapers-1080p-4k-2ifex-2560x1600.jpg') no-repeat center center/cover;
  position:absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
  z-index:-1;
}
.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 9rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}

.bold {
  font-weight: 700;
}





